import { Button, Card, Typography } from '@mui/material';
import React from 'react';

const AchievementCard = (props: any) => {
  return (
    <Card
      className='px-3 py-4 my-3 bounce-box bounce-5'
      style={{ minWidth: '280px', maxWidth: '300px' }}
    >
      <Typography variant='h5'>Maths and logic hackathon</Typography>
      <Typography variant='h6'>2021</Typography>
      <Typography variant='h6'>Runners Up</Typography>
      <Typography variant='body1'>
        I was a member of the team Terabyte, became the runners up in the Maths
        and Logic Hackathon organized by the University of Westminster.
      </Typography>
    </Card>
  );
};

export default AchievementCard;
