import { t } from 'i18next';
import React from 'react';
import { PageHeading } from '../../Atoms/PageHeading';
import AchievementCard from '../../Atoms/AchievementCard';
import { LargeCard } from '../../Atoms/LargeCard';

const getTranslationKey = (key: String) => {
  console.log(key);
  return t(`ACHIEVEMENTS.${key}`);
};
const AchievementsPage = () => {
  const AchievementPageContent = () => {
    return (
      <div className='px-md-5 px-sm-3  d-flex  flex-wrap justify-content-around'>
        <AchievementCard />
        <AchievementCard />
        <AchievementCard />
        <AchievementCard />
      </div>
    );
  };
  return (
    <div id='achievements' className='d-flex flex-column align-items-center'>
      <PageHeading title={getTranslationKey('TITLE')} />
      <LargeCard children={<AchievementPageContent />} noBackgroundGray />
    </div>
  );
};

export default AchievementsPage;
